.checkbox{display:flex;align-items:center;gap:2.5rem;position:relative;font-size:.9rem}.checkbox__input{cursor:pointer;height:0;width:0}.checkbox__input::before{display:flex;align-items:center;justify-content:center;text-rendering:auto;-webkit-font-smoothing:antialiased;font:var(--fa-font-solid);content:"";color:#0d94fa;position:absolute;left:0;top:50%;transform:translateY(-50%);width:25px;height:25px;text-align:center;transition:.5s;background:#273852;border-radius:8px}.checkbox__input:checked::before{content:""}.checkbox__text{cursor:pointer}