@use 'scss/index' as *;

.text-area-input {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    height: 100%;

    &__label {
        font-size: 0.9rem;
    }

    &__textarea {
        resize: none;
        border: 1px solid $blue-dark-accent;
        border-radius: 8px;
        outline: none;
        font-size: 1rem;
        margin-bottom: 1rem;
        padding: 0.75rem 1rem;
        color: inherit;
        min-height: 150px;
        height: 100%;
        background: $blue-dark-accent;
        transition: $transition-default;
        font-family: 'Roboto', sans-serif;

        &:focus {
            border: rgba($blue, 1) 1px solid;
        }
    }
}
